* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    text-decoration: none;
    list-style: none; }

html, body, #root, .main {
    width: 100%;
    height: 100%;
    min-width: 350px;
    overflow-x: hidden; }

.main {
    position: absolute;
    display: flex;
    flex-direction: column; }




